.layout-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.layout-container .layout-main {
  height: -webkit-fill-available;
  min-height: 400px;
  padding-top: 58px;
  margin-bottom: -450px;
  padding-bottom: 450px;
  position: relative;
}
.layout-container .ant-layout-has-sider {
  height: 100%;
}
.layout-container .nodata-page {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 45px;
  height: 100%;
  z-index: 2;
}
@media screen and (max-width: 1000px) {
  .header-wrap {
    display: none;
  }
  .header-mobile-container {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
  }
  .layout-container .layout-main {
    padding-top: 48px;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}



