.button-group {
  display: inline-block;
  width: 70px;
  height: 24px;
  font-size: 12px;
  line-height: 24px;
  text-align: center;
  border: 1px solid #5F86FB;
  border-radius: 3px;
  color: #5F86FB;
  background: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.button-group:hover {
  background: #5F86FB;
  color: #fff;
  cursor: pointer;
}
.button-group.search-btn {
  border: 1px solid #fff;
  color: #fff;
}
.button-group.search-btn:hover {
  background: #fff;
  color: #5F86FB;
}
.button-group.bg-btn {
  width: 90px;
  border: 1px solid #fff;
  color: #fff;
}
.button-group.bg-btn:hover {
  background: #fff;
  color: #5F86FB;
}
.button-group.mobile-btn {
  background: #5F86FB;
  -webkit-box-shadow: 0 2px 4px 0 #4F49DB;
          box-shadow: 0 2px 4px 0 #4F49DB;
  border-radius: 3px;
  border: none;
  color: #fff;
  font-size: 10px;
}
@media (min-width: 2560px) {
  .button-group.search-btn {
    border: 1px solid #fff;
    color: #fff;
  }
  .button-group.search-btn:hover {
    background: #fff;
    color: #5F86FB;
  }
  .button-group.bg-btn {
    width: 157px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
  }
}



