.footer-group {
  width: 100%;
  min-width: 1128px;
  height: 4.5rem;
  min-height: 450px;
  background: #5F86FB;
  color: #fff;
}
.footer-group .footer-wrap {
  background: #5F86FB;
  min-width: 1088px;
  width: 10.88rem;
  margin: 0 auto;
  padding: 0 20px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.footer-group .footer-wrap .footer-smallbox {
  -webkit-flex: 1 1;
      -ms-flex: 1 1;
          flex: 1 1;
  width: 3.5rem;
  height: 3rem;
  min-width: 350px;
  min-height: 300px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(1%, #4AB6F6), to(#6858ED));
  background-image: -webkit-linear-gradient(top, #4AB6F6 1%, #6858ED 100%);
  background-image: -o-linear-gradient(top, #4AB6F6 1%, #6858ED 100%);
  background-image: linear-gradient(-180deg, #4AB6F6 1%, #6858ED 100%);
  -webkit-box-shadow: 0 2px 10px 0 #3491E8;
          box-shadow: 0 2px 10px 0 #3491E8;
  border-radius: 3px;
  margin-right: 0.2rem;
  margin-top: 0.6rem;
}
.footer-group .footer-wrap .footer-smallbox:last-child {
  margin-right: 0;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-title {
  border-bottom: 1px solid #5791F3;
  color: #fff;
  padding: 0.3rem;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-title h3 {
  font-size: 20px;
  font-weight: 500;
  font-family: NotoSansHans-Bold;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content .smallbox-logo {
  width: 2rem;
  height: 0.5rem;
  margin: 0 auto;
  margin-top: 0.3rem;
  margin-bottom: 0.45rem;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content .smallbox-logo img {
  width: 100%;
  height: 100%;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content .smallbox-text {
  font-family: NotoSansHans-Regular;
  text-align: center;
  font-size: 14px;
  line-height: 0.2rem;
  width: 210px;
  margin: 0 auto;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li {
  margin-top: 0.2rem;
  margin-left: 0.3rem;
  font-size: 14px;
  font-family: NotoSansHans-Regular;
  position: relative;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li img {
  width: 0.3rem;
  height: 0.3rem;
  min-width: 30px;
  min-height: 30px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.1rem;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li span,
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li a {
  vertical-align: middle;
  color: #fff;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li span:hover,
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li a:hover {
  cursor: pointer;
  color: #B6C9FF;
  text-decoration: underline;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li .code-wrap {
  width: 80px;
  height: 80px;
  min-width: 80px;
  box-shadow: none;
  -webkit-box-shadow: none;
  border: none;
  background: none;
  position: absolute;
  top: -80px;
  left: 30px;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li .code-wrap img {
  width: 100%;
  height: 100%;
  margin-right: 0;
}
.footer-group .footer-wrap .footer-smallbox .smallbox-content ul li .code-wrap::after {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top: 5px solid #fff;
  border-radius: 2px;
  position: absolute;
  bottom: -10px;
  left: 50%;
  margin-left: -5px;
}
.footer-group .footer-version {
  text-align: center;
  font-family: NotoSansHans-Regular;
  font-size: 14px;
  color: #fff;
  margin-top: 0.32rem;
  line-height: 22px;
}
@media (min-width: 2560px) {
  .footer-group .footer-wrap .footer-smallbox .smallbox-title h3 {
    font-size: 24px;
  }
  .footer-group .footer-wrap .footer-smallbox .smallbox-content .smallbox-text {
    font-size: 18px;
  }
  .footer-group .footer-wrap .footer-smallbox .smallbox-content ul li {
    font-size: 18px;
  }
  .footer-group .footer-version {
    font-size: 18px;
    line-height: 26px;
  }
}
@media (max-width: 768px) {
  .footer-group {
    min-width: 3.75rem;
    height: 48px;
    min-height: 48px;
    background: #5F86FB;
    color: #fff;
    position: absolute;
    bottom: 0;
  }
  .footer-group .footer-wrap {
    display: none;
  }
  .footer-group .footer-version {
    padding-top: 3px;
    margin-top: 0;
  }
}



