.input-group {
  font-size: 14px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  width: 100%;
  text-align: center;
}
.input-group .input-icon {
  border: none;
  background: none;
  border-bottom: 1px solid #333;
  width: 100%;
  font-family: NotoSansHans-Regular;
  font-size: 16px;
  color: #333;
  padding: 10px;
  text-align: center;
}
.input-group .input-icon::-webkit-input-placeholder {
  font-family: NotoSansHans-Regular;
  font-size: 16px;
  color: #B2B2B2;
}
.input-group .top-search {
  color: #fff;
  border-bottom: 1px solid #fff;
}
.input-group .top-search::-webkit-input-placeholder {
  color: #fff;
}



